
import React, { useState } from 'react'
import { useNavigate } from 'react-router-dom';
import { useRecoilValue } from 'recoil';
import Swal from 'sweetalert2';
import Modal from './component/Modal';
import { loginInfoAtom } from './State';
import axiosInstance from '../utils/AxiosHandler'


const pointKeys = [
    "ironInitialPoint",
    "bronzeInitialPoint",
    "silverInitialPoint",
    "goldInitialPoint",
    "platinumInitialPoint",
    "diamondInitialPoint",
    "masterInitialPoint",
    "grandmasterInitialPoint",
    "challengerInitialPoint",
]
const minPointKeys = [
    "ironMinPoint",
    "bronzeMinPoint",
    "silverMinPoint",
    "goldMinPoint",
    "platinumMinPoint",
    "diamondMinPoint",
    "masterMinPoint",
    "grandmasterMinPoint",
    "challengerMinPoint"
]


export default function CreateAuction() {
    const navigate = useNavigate();
    const loginName = useRecoilValue(loginInfoAtom)
    const [isOpen, setIsOpen] = useState(false)
    const [isOpenMinPoint, setIsOpenMinPoint] = useState(false)
    const [auctionInfo, setAuctionInfo] = useState({
        maxTeam: 0,
        ironInitialPoint: null,
        bronzeInitialPoint: null,
        silverInitialPoint: null,
        goldInitialPoint: null,
        platinumInitialPoint: null,
        diamondInitialPoint: null,
        masterInitialPoint: null,
        grandmasterInitialPoint: null,
        challengerInitialPoint: null,
        ironMinPoint: null,
        bronzeMinPoint: null,
        silverMinPoint: null,
        goldMinPoint: null,
        platinumMinPoint: null,
        diamondMinPoint: null,
        masterMinPoint: null,
        grandmasterMinPoint: null,
        challengerMinPoint: null,
    })

    const validatePointInput = () => {
        for (let index = 0; index < pointKeys.length; index++) {
            if (!auctionInfo[pointKeys[index]]) {
                return false
            }
        }
        return true
    }
    const validateMinPointInput = () => {
        for (let index = 0; index < minPointKeys.length; index++) {
            if (!auctionInfo[minPointKeys[index]]) {
                return false
            }
        }
        return true
    }
    const updateAuctionInfo = (e) => {
        setAuctionInfo({
            ...auctionInfo,
            [e.target.name]: e.target.value
        })
    }

    const openModal = () => {
        setIsOpen(true)
    }

    const openModalMinPoint = () => {
        setIsOpenMinPoint(true)
    }

    const closeModal = (e) => {
        e.preventDefault()
        setIsOpen(false)
    }
    const closeModalMinPoint = (e) => {
        e.preventDefault()
        setIsOpenMinPoint(false)
    }

    const postAuctionInfo = () => {
        if (!auctionInfo.maxTeam) {
            Swal.fire('!', "   !", "warning")
            return;
        }
        if (!(validatePointInput() && validateMinPointInput())) {
            Swal.fire('!', "    !", "warning")
            return;
        }

        axiosInstance.post(`/auction`, {
            ...auctionInfo,
            ownerToken: loginName.token
        }).then(response => {
            Swal.fire({
                title: "!",
                html: ` <br/> <b>` + response.data.data.auctionId + `</b><br/>!`,
                icon: 'success',
                showConfirmButton: true,
                confirmButtonText: ""
            }).then(res => {
                if (res.isConfirmed) {
                    navigator.clipboard.writeText(response.data.data.auctionId)
                }
            })
            navigate('/')
        })
    }


    return (
        <div className="create">
            <div className="container">
                <h1 className="title">{loginName.twitchName} !<br />
                       !
                </h1>
                <div className="form">
                    <div className="form-group guide">   * 5       !<br/> !</div>
                    <div className="form-group">
                        <div className="input-title"> </div>
                        <div className="input-box">
                            <input type="number" placeholder=" !" name="maxTeam" onChange={updateAuctionInfo} pattern="[0-9]+"></input>
                        </div>
                    </div>
                    <div className="point-setting form-group">
                        <div className="point-title"> </div>

                        <div className="point-btn input-box">
                            <button onClick={openModalMinPoint}>  </button>

                            <button onClick={openModal}> </button>
                        </div>
                    </div>
                    <div className="btn-box">
                        <button className="btn-border main" onClick={() => { navigate('/') }}></button>
                        <button className="btn-border" onClick={() => { postAuctionInfo() }}> </button>
                    </div>
                </div>
            </div>
            <div className="pointModal">
                <Modal isOpen={isOpenMinPoint} closeModal={closeModalMinPoint}>
                    <div className="point-title">  </div>
                    <div className="input-box">
                        <input type="number" placeholder="" name="ironMinPoint" value={auctionInfo.ironMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="bronzeMinPoint" value={auctionInfo.bronzeMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="silverMinPoint" value={auctionInfo.silverMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="goldMinPoint" value={auctionInfo.goldMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="platinumMinPoint" value={auctionInfo.platinumMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="diamondMinPoint" value={auctionInfo.diamondMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="masterMinPoint" value={auctionInfo.masterMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="grandmasterMinPoint" value={auctionInfo.grandmasterMinPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="challengerMinPoint" value={auctionInfo.challengerMinPoint} onChange={updateAuctionInfo}></input>
                    </div>
                    <div className="btn-box">
                        <button className="btn-border" onClick={closeModalMinPoint}> </button>
                    </div>
                </Modal>
            </div>

            <div className="pointModal">
                <Modal isOpen={isOpen} closeModal={closeModal}>
                    <div className="point-title"> </div>
                    <div className="input-box">
                        <input type="number" placeholder="" name="ironInitialPoint" value={auctionInfo.ironInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="bronzeInitialPoint" value={auctionInfo.bronzeInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="silverInitialPoint" value={auctionInfo.silverInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="goldInitialPoint" value={auctionInfo.goldInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="platinumInitialPoint" value={auctionInfo.platinumInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="diamondInitialPoint" value={auctionInfo.diamondInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="masterInitialPoint" value={auctionInfo.masterInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="grandmasterInitialPoint" value={auctionInfo.grandmasterInitialPoint} onChange={updateAuctionInfo}></input>
                        <input type="number" placeholder="" name="challengerInitialPoint" value={auctionInfo.challengerInitialPoint} onChange={updateAuctionInfo}></input>
                    </div>
                    <div className="btn-box">
                        <button className="btn-border" onClick={closeModal}> </button>
                    </div>
                </Modal>
            </div>
        </div>
    )
}
